.header_outer_container{
    position: sticky;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(20,20,20);
    /* background-color: transparent; */
    height: 10vh;
    color: white;
    transition: all 0.5s;
    transition-timing-function: ease-in;
    z-index: 1;
    /* background-color: red; */
}

.nav_black{
    background-color: rgb(20,20,20);
}
.header_container{
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header_left ul {

     width: 140%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-decoration: none;
    list-style-type: none;
    cursor: pointer;
    /* background-color: lightblue; */
}

.header_right{
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 20px;

}